<template>
  <div class="login-frame">
    <div class="logo-wrap">
      <a v-link="{path:'/account' }">
        <img src="../assets/img/return.png" class="return" alt="返回" />
      </a>
      <img v-bind:src="v_mp_avatar" class="logo-img" alt="华科学习帝" />
      <p>{{ v_mp_name }}</p>
    </div>
    <div class="login-form-wrap">
      <p class="error-msg" v-show="v_err">信息输入有误</p>
      <div class="input-wrap">
        <input class="input-text mp" type="text" placeholder="邮箱/微信号/QQ号" v-model="v_phone">
      </div>
      <div class="input-wrap">
        <input class="input-password mp" type="password" v-on:keyup.enter="f_login" placeholder="密码" v-model="v_pwd">
      </div>
     <div class="input-wrap" v-show="v_show_img">
       <input class="input-img-code mp" type="text" v-on:keyup.enter="f_login" v-model="v_imageCode">
       <img class="img-code" v-bind:src="v_code" @click = "f_code"/>
     </div>
    </div>
    <div class="login-button-wrap">
      <div class="input-wrap input-button-wrap">
        <input class="input-button mp" v-bind:class="{ 'disable': v_button_disable}" type="button" v-model="v_button_msg" v-on:keyup.enter="f_login" @click="f_login">
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import { Config } from "../config.js"

export default {
  data () {
    return {
      v_mp_name: localStorage.getItem('mp_name'),
      v_mp_avatar: localStorage.getItem('mp_avatar'),
      v_phone: '',
      v_pwd: '',
      v_err: false,
      v_msg: "信息有误",
      v_show_img:false,
      v_code:'',
      v_imageCode:"",
      v_button_disable : false,
      v_button_msg:"登录"
    }
  },
  methods:{
    f_login: function(){
      if(this.v_button_disable)
        return;

      if(this.v_phone == ""||this.v_pwd == "")
        return (this.v_err = true) && (this.v_msg = "用户名或密码输入错误，请检查信息")

      if(this.v_show_img&&this.v_imageCode=="")
        return (this.v_err=true) && (this.v_msg = "请输入验证码")

      this.v_button_msg = "正在登录..."
      this.v_button_disable = true

      var data={
        username: this.v_phone,
        password: this.v_pwd,
        imageCode:this.v_imageCode
      }
      this.$http.post(Config.accountHost+'/api/login',data).then(function(response){
        var data=JSON.parse(response.body);

        this.v_button_msg = "登录";
        this.v_button_disable = false;

        if(data.base_resp.ret==200008){
          this.v_imageCode=true;
          this.v_code = Config.accountHost+"/api/verifycode?t="+ Math.random()*5;
          return
        }

        if(data.base_resp.ret!=0){
          this.v_err=true;
          this.v_msg="信息错误,请重新输入";
          if(this.v_imageCode){
            this.f_code();
          }
          return
        }
        this.$router.go({path:'/qrcode'});
      },function(){
          this.v_msg = "系统错误";
          this.v_button_msg = "请重试";
          this.v_button_disable = false;
          return this.v_err=true;
      })
    },
    f_code:function(){
      this.v_code = Config.accountHost + "/api/verifycode?t="+ new Date().getTime()
    }
  }
}
</script>

<style lang="scss" scoped>
  .login-frame{
    box-sizing:border-box;
    background-color:#fff;
    padding-top:40px;
    margin:auto;
    width:100%;
    height:100%;
    border-radius:2px;
    position:relative;
  }

  //logo的框
  .logo-wrap{
    text-align:center;
    position:relative;
    .return{
      position:absolute;
      top:0;
      left:10%;
      height:23px;
    }
    .logo-img{
      width:93px;
      border-radius:50%;
    }
    p{
      font-size:16px;
    }
  }

  .login-form-wrap{
    width:80%;
    position:relative;
    padding-top:50px;
    margin:auto;

    .error-msg{
      position:absolute;
      top:20px;
      color:#E68989;
    }
    .input-wrap{
      margin-bottom:10px;
    }
  }
  .login-button-wrap{
    width:80%;
    position:absolute;
    left:10%;
    bottom:20px;
  }
</style>



