<template>
  <div class="add-wrap content-wrap">
    <div class="add-header header">
      <img src='../assets/img/return.png' @click="f_back" class="return">
    </div>
    <div class="content">
      <img v-bind:src="v_code" class="qrcode" alt="二维码" />
      <!-- <img src="../assets/img/qrcode.png" class="qrcode" alt="二维码" /> -->
      <p class="msg">{{v_msg}}</p>
    </div>
  </div>
</template>

<script>
import { Config } from "../config.js"

  export default {
    data () {
      return {
				v_code: '',
				v_msg: '用微信扫码验证身份后，可绑定公众号',
				v_back: false
      }
    },
    route:{
      data(){
        this.v_code='';
        this.v_back=false;
      }
    },
    methods:{
			f_scan: function(){
				var self=this;
				self.$http.get(Config.accountHost + '/api/safeuuid?1=1').then(function(response){
					if(this.v_back)
					  return;
					var data=JSON.parse(response.body);
					if(data.errcode=="401" || (data.base_resp !=null && data.base_resp.ret == "401")){
						setTimeout(function(){
							self.f_scan();
						},3000)
					}
					else if (data.errcode=="404"){
					  self.v_msg="扫描成功请在微信上点击确认！";
						self.f_scan()
					}
					else if(data.errcode=="405")
					  self.f_succ();
				})
			},
			f_succ: function(){
				this.$http.get(Config.accountHost+'/api/securewxverify').then(function(response){
					var data=JSON.parse(response.body);
					if(data.base_resp.ret==0)
					  this.$router.go({path: 'account'})
				},function(){

				})
			},
			f_back: function(){
				this.v_back=true;
				this.$router.go({path: '/mplogin'});
			}
    },
		route: {
			data() {
        this.v_code = Config.accountHost + '/api/qrcode?'+ Math.random()*5;
        var _this = this;
				setTimeout(function(){_this.f_scan()},3000);
			}
		},
		ready: function(){

		}
  }
</script>

<style lang='scss' scoped>
  .add-wrap{
    padding-top:80px;
  }
  .add-header{
    position:relative;
    img{
      cursor: pointer;
    }
    .return{
      position: absolute;
      height:22px;
      top:-40px;
      left:10%;
      cursor: pointer;
    }
  }
  .content{
    text-align:center;
    .qrcode{
      width:80%;
    }
    .msg{
      height: 20px;
      font-size: 14px;
    }
  }

  .register-msg{
    text-align: center;
  }
</style>



