<template>
  <div class="login-frame">
    <div class="logo-wrap">
      <img src="../assets/img/logo.png" class="logo-img" alt="新媒体管家" />
      <p>登录新媒体管家账号</p>
    </div>
    <div class="login-form-wrap">
      <p class="error-msg" v-show="v_err">信息输入有误</p>
      <div class="input-wrap">
        <input class="input-text" type="text" placeholder="手机号" v-model="v_phone">
      </div>
      <div class="input-wrap">
        <input class="input-password" type="password" v-on:keyup.enter="f_login" placeholder="密码" v-model="v_pwd">
      </div>
     <div class="input-wrap" v-show="v_show_img">
       <input class="input-img-code" type="text" v-on:keyup.enter="f_login" v-model="v_imageCode">
       <img class="img-code" v-bind:src="v_code" @click = "f_code"/>
     </div>
    </div>
    <div class="login-button-wrap">
      <div class="input-wrap input-button-wrap">
        <input class="input-button" v-bind:class="{ 'disable': v_button_disable}" type="button" v-model="v_button_msg" v-on:keyup.enter="f_login" @click="f_login">
      </div>
      <ul class="bom-function">
        <li class="pwd"><a href="javascript:void(0)" data-href="http://sso.xmt.cn/goFindPwd" @click = 'openExternalLink'>忘记密码</a></li>
        <li class="reg"><a href="javascript:void(0)" data-href="http://sso.xmt.cn/goRegister" @click = 'openExternalLink'>注册帐号</a></li>
      </ul>
    </div>
  </div>
</template>
<script type="text/javascript">
import { Config } from "../Config"
import { util } from "../util"
// import { shell } from 'electron'
// var electron = require("electron")

export default {
  data () {
    return {
      v_phone: '13122110058',
      v_pwd: 'a12345678',
      v_err: false,
      v_msg: "信息有误",
      v_show_img:false,
      v_code:'',
      v_imageCode:"",
      v_button_disable : false,
      v_button_msg:"登录"
    }
  },
  methods:{
    f_login: function(){
      if(this.v_button_disable)
        return;

      if(this.v_phone == ""||this.v_pwd == "")
        return (this.v_err = true) && (this.v_msg = "用户名或密码输入错误，请检查信息")

      if(this.v_show_img&&this.v_imageCode=="")
        return (this.v_err=true) && (this.v_msg = "请输入验证码")

      this.v_button_msg = "正在登录..."
      this.v_button_disable = true

      var data={
        phonenumber: this.v_phone,
        password: this.v_pwd,
        imgcode:this.v_imageCode,
        rememberPwd:'on',
        referUrl:Config.accountHost+'/api/a',
      }
      this.$http.post(Config.loginHost+'/goLoginChk',data).then(function(response){
        var data =JSON.parse(response.body),
           _this = this;
        if(data.status){
          this.$http.get(data.data).then(function(){
            this.v_button_msg = "登录成功";
            window.localStorage.setItem("phone",this.v_phone);
            window.localStorage.setItem("password",this.v_pwd);
            this.$router.go({path:'/account/'+this.v_phone})
          });
        }else{
          this.v_err = true;
          this.v_button_msg = "请重试";
          this.v_button_disable = false;
          if(data.data.substr(0,5) == "false"){
            //用户名或者密码错误
            this.v_msg = data.data.substr(6,20);
            if(this.v_show_img){
              this.f_code();
            }
          }else{
            //需要输入验证码
            this.v_msg = data.data.substr(5,20);
            this.v_show_img = true;
            this.v_code = Config.loginHost + "/valCode?t="+new Date().getTime()
          }
        }
      },function(){
          this.v_msg = "系统错误";
          this.v_button_msg = "请重试";
          this.v_button_disable = false;
          return this.v_err=true;
      })
    },
    parseUrl:function(url){
      var a =  document.createElement('a');
      a.href = url;
      return a.search;
    },
    f_code:function(){
      this.v_code = Config.loginHost+"/valCode?t="+new Date().getTime();
    },
    openExternalLink:function(event){
      util.openExternalLink(event.target.dataset.href);
    }

  }
}
</script>

<style lang="scss" scoped>
  .login-frame{
    box-sizing:border-box;
    background-color:#fff;
    padding-top:40px;
    margin:auto;
    width:100%;
    height:100%;
    border-radius:2px;
    position:relative;
  }

  //logo的框
  .logo-wrap{
    text-align:center;
    .logo-img{
      width:93px;
    }
    p{
      font-size:16px;
    }
  }

  .login-form-wrap{
    width:80%;
    position:relative;
    padding-top:50px;
    margin:auto;

    .error-msg{
      position:absolute;
      top:20px;
      color:#E68989;
    }
    .input-wrap{
      margin-bottom:10px;
    }
  }
  .login-button-wrap{
    width:80%;
    position:absolute;
    left:10%;
    bottom:20px;
  }
  .bom-function{
    margin-top:15px;
		li{
			list-style: none;
      &:first-child{
        float:left;
        text-align:left;
      }
      &:last-child{
        float:right;
      }
		}
		a{
			display: block;
      color:#333;
      &:hover,&:focus{
        text-decoration:underline;
      }
		}
	}
</style>



