<template>
  <div class="we-main">
    <ul class="left-menu">
      <li><a v-link="{path:'/account/'+v_account}" @click="menu_click" class="left-menu-item gzh active">公众号</a></li>
			<li><a href="javascript:void(0)" @click="menu_click" data-href="http://xmt.cn" class="left-menu-item pb">图文排版</a></li>
			<li><a v-link="{path:'/account/class'}" @click="menu_click" class="left-menu-item xmt">新媒体课堂</a></li>
			<li><a href="javascript:void(0)" @click="menu_click" data-href="http://xmt.cn" class="left-menu-item download">App下载</a></li>
			<li class="setting"><a href="javascript:void(0)" class="set"></a></li>
		</ul>
    <router-view> </router-view>
	</div>
</template>
<script>
import { util } from "../util"
import $ from 'jquery'
export default{
	data(){
    return{
      v_account:this.$route.params.account?this.$route.params.account:localStorage.getItem("phone"),
      v_title:""
    }
	},
  computed: {
    v_account_url:function(){
      return "#!/account/" + this.v_account
    }
  },
  route:{
    data(){
        console.log('没有运行');
      }
  },
  methods:{
    menu_click:(event)=>{
      var target = event.target;
      if(target.classList.contains("active"))
        return

      if(target.dataset.href)
        return util.openExternalLink(target.dataset.href)

      return $(".left-menu-item").removeClass("active") && target.classList.add("active")
    },
  },
  route:{
    data(){
    }
  }
}
</script>
<style lang="scss" scoped>
@import "../assets/css/main.scss";
</style>



