<template lang="html">
  <div id="main">
    <dl class="we-content-list rain-left" v-for ="result in results" data-id="{{result.id}}" data-username="{{result.username}}" data-type="{{result.type}}" data-name="{{result.name}}" data-avatar="{{result.portrait?result.portrait:default_avatar}}">
      <dt><img v-bind:src="result.portrait?result.portrait:default_avatar"></dt>
      <dd class="title">{{ result.name }}</dd>
      <dd>{{result.username}}</dd>
      <div class="context-menu">
        <ul>
          <li><a href="javascript:void(0)" @click="f_cancel(result.id,result.type)">解绑</a></li>
        </ul>
      </div>
    </dl>
  </div>
</template>
<script>
import $ from 'jquery'
import { Config } from "../config"
import { remote, ipcRenderer } from 'electron'

let ses = remote.getCurrentWebContents().session
let BrowserWindow = remote.BrowserWindow
let mpWindow = null

export default {
  data: function () {
    return {
      default_avatar: require("../assets/img/avatar.png"),
      container:'',
      menu: ''
    }
  },
  props:["results"],
  computed: {},
  attached: function () {},
  methods: {
    f_click:function(id,username,type,avatar,name){
      this.$http.get(Config.accountHost+'/api/get_cookies?aid='+id+"&type="+type).then(function(response){
        let data = JSON.parse(response.body)
        let _this = this
        if(data.base_resp.ret==-1000){
          console.log("-1000是什么意思？")
        }
        //向主线程发送消息
        ipcRenderer.send('check-mp-login',data.cookies);
        ipcRenderer.on('check-mp-login',function(event,isLogin){
          if(isLogin){
            //已经登录 由主进程打开公众号窗口
          }else{
            console.log('未登录公众号')
            window.localStorage.setItem("mp_name",name)
            window.localStorage.setItem("mp_avatar",avatar)

            _this.$router.go({path: '/mplogin' })
          }
        })
      },function(){
        // this.$router.go({path: '/'});
      })
    },
    f_cancel: function(id,type){
      let data={
        aid: id,
        type: type
      }
      self.$http.post(Config.accountHost+'/api/unbind',data).then(function(response){
        let data=JSON.parse(response.body)
        if(data.base_resp.ret==0)
          self.results.$remove(self.results.find(t=>t.id === id))
      })
    }
  },
  components: {},
  ready: function () {
   var self=this;
   $("body").click(function(){
     $(".context-menu").hide();
   })
   $("#main").delegate(".we-content-list","mousedown",function(e){
      let _this=$(this);
      if(e.button==0){
        $(".context-menu").hide();
        let id = _this.data("id");
        let username = _this.data("username")
        let type = _this.data("type")
        let avatar = _this.data("avatar")
        let name = _this.data("name")
        self.f_click(id,username,type,avatar,name)
      }
      else if(e.button ==2){
        let left=e.clientX;
        let top=e.clientY;
        if(left>300)
          left=190;
        else
          left=left-83;
        let f=parseInt(top/85);
        if(top>=144){
          let num=top-(f*85)
          if(num<55)
            top=top-(f*85)+20;
          else
            top=top-(f*85)-60;
        }
        else
          top=top-60;
        $(".context-menu").hide();
        _this.find('.context-menu').css({left:left,top:top});
        _this.find('.context-menu').show();
      }
    })
  }
}
</script>

<style lang="scss">
  .we-content-list{
  	height: 45px;
  	padding: 20px;
  	border-bottom:1px solid #ddd;
    position: relative;
    &:hover{
      cursor:pointer;
      background: linear-gradient(to bottom, #FFF, #EEE);
    }
  	dt img{
  		border-radius:50%;
      height:45px;
      widht:45px;
  	}
  	dd{
  		line-height: 22px;
  		margin-left: 15px;
  		width: 150px;
  		color: #999;
  		font-size: 12px;
  	}
  	.title{
  		color: #333;
  		font-size: 14px;
  	}
    .context-menu{
      position: absolute;
      z-index: 9999;
      height: 38px;
      width: 70px;
      cursor: pointer;
      display: none;
      ul{
        position: absolute;
        left: 0;
        z-index: 1000;
        float: left;
        padding: 5px 0;
        margin: 2px 0 0;
        list-style: none;
        background-color: #ffffff;
        border: 1px solid #ccc;
        border: 1px solid rgba(0, 0, 0, 0.2);
        -webkit-border-radius: 6px;
        -moz-border-radius: 6px;
        border-radius: 6px;
        -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
        -webkit-background-clip: padding-box;
        -moz-background-clip: padding;
        background-clip: padding-box;
      }
      li{
        line-height: 20px;
      }
      a{
        display: block;
        padding: 3px 20px;
        clear: both;
        font-weight: normal;
        line-height: 20px;
        color: #333333;
        white-space: nowrap;
      }
    }
  }
</style>



