<template lang="html">
  <div class="right-content">
    <div class="top">
      <h2>微信公众号</h2>
      <a href="javascript:void(0)" @click="f_add" class="add-mp"></a>
    </div>
    <div class="result-wrap">
      <Loading v-show="v_loading"> </Loading>
      <No_results v-show = "v_empty"> </No_results>
      <Result :results="results"></Result>
    </div>
  </div>
</template>

<script>
import No_results from "./No_results.vue"
import Loading from "./Loading.vue"
import {Config} from "../config.js"
import Result from "./Item.vue"
let mp_logo = require("../assets/img/mp_logo.png")

export default{
	data(){
		return{
			v_empty: false,
      v_account:'',
      v_loading:true,
      results:[]  //传给子组件的数据
		}
	},
  methods:{
    f_add:function(){
      localStorage.setItem("mp_name","绑定微信公众号")
      localStorage.setItem("mp_avatar",mp_logo)
      this.$router.go({path: '/mplogin'})
    }
  },
  route:{
    data(){
      if(this.$route.params.account!=undefined)
        this.v_account = this.$route.params.account

      this.$http.get(Config.loginHost+"/verify?referUrl="+encodeURI(Config.accountHost+"/api/bind/users"))
        .then(function(response){
          try{
            var data = JSON.parse(response.body);
          }catch(e){
            return this.$router.go({path: '/login'})
          }
          if(data.base_resp.ret==-3)
            return this.$router.go({path: '/login'})
          if(data.base_resp.ret == 0){
            this.v_account=data.phone
            if(data.users.length == 0){
              this.v_loading = false
              this.v_empty = true
            }else{
              this.v_loading = false
              this.v_empty = false
              this.results = data.users
            }
          }else{
            //出错
          }
        },function(){

        })
    }
  },
  components:{
    No_results,Result,Loading
  }
}
</script>

<style lang="scss" scoped>
.right-content{
	float: left;
	width: 75%;
	background-color: #fff;
	height: 480px;
	position:relative;
	.top{
		height: 60px;
		background: #F8F8F8;
		box-sizing:border-box;
		border-bottom:1px solid #ccc;
		.add-mp{
			cursor:pointer;
			background: url('../assets/img/add.png') no-repeat center;
			background-size: 16px;
			position: absolute;
			height:16px;
			width:16px;
			top: 22px;
			color: #999;
			right: 15px;
      z-index:120;
		}
	}
	h2{
		text-align: center;
		font-size: 14px;
		font-weight:normal;
		line-height: 60px;
	}
  .result-wrap{
    height:420px;
    overflow:auto;
    &::-webkit-scrollbar {
      width: 4px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: lighten(#1e7be2,15%);
      border-radius:2px;
    }
  }
}
</style>



